// nouislider 9.2.0
.noUi-target,
.noUi-target * {
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  touch-action: none;
  user-select: none;
}
.noUi-target {
  position: relative;
  direction: ltr;
}
.noUi-base {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}
.noUi-connect {
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
}
.noUi-origin {
  position: absolute;
  height: 0;
  width: 0;
}
.noUi-handle {
  position: relative;
  z-index: 1;
}
.noUi-state-tap .noUi-connect,
.noUi-state-tap .noUi-origin {
  transition: top 0.3s, right 0.3s, bottom 0.3s, left 0.3s;
}
.noUi-state-drag * {
  cursor: inherit !important;
}
// Painting and performance; Browsers can paint handles in their own layer.
.noUi-base,
.noUi-handle {
  transform: translate3d(0, 0, 0);
}
// Slider size and handle placement
.noUi-horizontal {
  height: 4px;
}
.noUi-horizontal .noUi-handle {
  left: -10px;
  top: -8px;
  width: 20px;
  height: 20px;
  border: 4px solid #ff0000;
  background-color: #fff;
}
// Styling
.noUi-target {
  background-color: #ebebeb;
}
.noUi-connect {
  background-color: #000;
  transition: background 450ms;
}
// Handles and cursors
.noUi-draggable {
  cursor: ew-resize;
}
.noUi-vertical .noUi-draggable {
  cursor: ns-resize;
}
.noUi-handle {
  border-radius: 50%;
  background: #fff;
  cursor: default;
}
// Disabled state;
[disabled] .noUi-connect {
  background: #b8b8b8;
}
[disabled].noUi-target,
[disabled].noUi-handle,
[disabled] .noUi-handle {
  cursor: not-allowed;
}
// Horizontal layout
.noUi-pips-horizontal {
  padding: 10px 0;
  height: 80px;
  top: 100%;
  left: 0;
  width: 100%;
}
.noUi-value-horizontal {
  transform: translate3d(-50%, 50%, 0);
}
