@font-face {
  font-family: "Meta Pro";
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/MetaPro-Normal.woff') format('woff'),
  url('../fonts/MetaPro-Normal.ttf') format('truetype')
}

@font-face {
  font-family: "Meta Pro";
  font-style: italic;
  font-weight: 400;
  src: url('../fonts/MetaPro-NormalItalic.woff') format('woff'),
  url('../fonts/MetaPro-NormalItalic.ttf') format('truetype')
}

@font-face {
  font-family: "Meta Pro";
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/MetaPro-Bold.woff') format('woff'),
  url('../fonts/MetaPro-Bold.ttf') format('truetype')
}

@font-face {
  font-family: "Meta Pro";
  font-style: italic;
  font-weight: 700;
  src: url('../fonts/MetaPro-BoldItalic.woff') format('woff'),
  url('../fonts/MetaPro-BoldItalic.ttf') format('truetype')
}

@font-face {
  font-family: "Neo Sans Pro";
  font-style: normal;
  font-weight: 700;
  src: url('../fonts/NeoSansPro-Bold.woff') format('woff'),
  url('../fonts/NeoSansPro-Bold.ttf') format('truetype')
}
