.products-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -22px;
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0 0 44px;
    padding-bottom: 30px;
    background-color: $white;
    border: 3px solid transparent;
    @media (min-width: 640px) {
      width: calc(50% - 44px);
      margin: 0 22px 44px;
    }
    @media (min-width: 1050px) {
      width: calc(33.3333% - 44px);
    }
    @media (min-width: 1200px) {
      width: calc(25% - 44px);
    }
    @media (min-width: 1680px) {
      width: calc(20% - 44px);
    }
    @media (min-width: 2200px) {
      width: calc(16.6667% - 44px);
    }
    &:hover {
      .products-list__over {
        display: block;
      }
    }
  }
  &__img {
    img {
      width: 100%;
      height: auto;
    }
  }
  &__info {
    padding: 0 20px;
  }
  &__name,
  &__color {
    color: $black;
    font-family: $sans-serif;
    font-size: 14px;
    font-weight: 700;
    line-height: 18px;
  }
  &__color {
    margin-bottom: 20px;
  }
  &__price {
    display: inline-block;
    font-family: $helper-font;
    font-weight: 700;
    white-space: nowrap;
    &._new {
      margin-right: 10px;
      color: $red;
      font-size: 24px;
      span {
        font-size: 21px;
      }
    }
    &._old {
      position: relative;
      color: #afafaf;
      font-size: 17px;
      line-height: 28px;
      &:after {
        content: '';
        position: absolute;
        top: 11px;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #afafaf;
      }
    }
  }
  &__btn {
    display: block;
    width: 100%;
    margin-bottom: 18px;
    padding: 10% 10px;
    color: $white;
    background-color: $red;
    font-family: $sans-serif;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    border: 1px solid $red;
    transition: color $slow, background-color $slow;
    &:hover {
      color: $red;
      background-color: $white;
    }
  }
  &__over {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% + 6px);
    margin: -3px;
    padding-bottom: 20px;
    background-color: $white;
    border: 3px solid $black;
    z-index: 100;
  }
}
