input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

p {
  margin: 0;
  padding: 0;
}

p.text {
  color: #4c4b4b;
  font-size: 17px;
  font-weight: 400;
  line-height: 25px;
}

p + p {
  margin-top: 20px;
}

a {
  color: #000;
  text-decoration: none;
  transition: color 0.3s;
}

a:hover {
  color: #ff0000;
}

textarea {
  resize: vertical;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1277px;
  margin: 0 auto;
  padding: 26px;
}

@media (min-width: 640px) {
  .container {
    padding: 48px;
  }
}

@media (min-width: 768px) {
  .container {
    padding: 68px;
  }
}

.block {
  padding: 20px 20px;
  background-color: #ececec;
}

@media (min-width: 600px) {
  .block {
    padding: 40px 48px;
  }
}

.block-cut__toggle {
  position: absolute;
  opacity: 0;
  display: none;
}

.block-cut__toggle:checked ~ .block-cut__title {
  display: none;
}

.block-cut__toggle:checked ~ .block-cut__content {
  height: auto;
}

.block-cut__title {
  display: block;
  padding-top: 20px;
  color: #ff0000;
  font-size: 17px;
  font-weight: 400;
  text-decoration: underline;
  cursor: pointer;
}

@media (min-width: 900px) {
  .block-cut__title {
    display: none;
  }
}

.block-cut__content {
  height: 0;
  overflow: hidden;
}

@media (min-width: 900px) {
  .block-cut__content {
    height: auto;
  }
}

.overlay {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.85);
  cursor: pointer;
  z-index: 99;
}

@media (max-width: 1049px) {
  .overlay._show {
    display: block;
  }
}

@font-face {
  font-family: "Meta Pro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/MetaPro-Normal.woff") format("woff"), url("../fonts/MetaPro-Normal.ttf") format("truetype");
