.title-h1,
.title-h2,
.title-h3 {
  color: #000;
  font-family: "Meta Pro", "Roboto", "Source Sans Pro", "Open Sans", Arial, sans-serif;
}

.title-h1 {
  margin-top: 0;
  font-size: 28px;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .title-h1 {
    font-size: 32px;
    line-height: 36px;
  }
}

@media (min-width: 1024px) {
  .title-h1 {
    font-size: 45px;
    line-height: 47px;
  }
}

.title-h2 {
  font-size: 22px;
  font-weight: 700;
  line-height: 26px;
}

@media (min-width: 768px) {
  .title-h2 {
    font-size: 36px;
    line-height: 38px;
  }
}

.title-h3 {
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
}

.btn {
  display: inline-block;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.btn._close {
  width: 20px;
  height: 20px;
  background-color: #fff;
  border: 0 none;
  border-radius: 0;
}

.btn._close:before, .btn._close:after {
  position: absolute;
  content: '';
  top: 9px;
  left: 1px;
  width: 18px;
  height: 3px;
  background-color: #e4e4e4;
  transition: background-color 0.3s ease-in-out;
}

.btn._close:before {
  transform: rotate(45deg);
}

.btn._close:after {
  transform: rotate(-45deg);
}

.btn._close:hover:before, .btn._close:hover:after {
  background-color: #ff0000;
}

.btn._default {
  width: 100%;
  padding: 33px 0;
  background-color: #d6d6d6;
  font-size: 20px;
  border: 0 none;
}

.btn._default:hover {
  background-color: rgba(214, 214, 214, 0.7);
}

.btn._accent {
  width: 100%;
  padding: 33px 0;
  color: #fff;
  background-color: red;
  font-size: 21px;
  font-weight: 700;
  border: 0 none;
  text-transform: uppercase;
}

.btn._accent:hover {
  background-color: rgba(255, 0, 0, 0.7);
}

.input {
  position: relative;
  width: 100%;
  height: 72px;
  padding: 0 10px 0 85px;
  border-radius: 4px;
  background-color: #fff;
}

.input input {
  width: 100%;
  height: 100%;
  border: 0 none;
  line-height: 24px;
}

.input input::placeholder {
  color: #b1b1b1;
  font-family: "Meta Pro", "Roboto", "Source Sans Pro", "Open Sans", Arial, sans-serif;
  font-size: 24px;
}

.input:before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 76px;
  height: 72px;
  background-repeat: no-repeat;
  background-position: 50%;
}

.input._name:before {
  background-image: url(../images/icon-man.png);
}

.input._phone:before {
  background-image: url(../images/icon-mobile.png);
}

.input._email:before {
  background-image: url(../images/icon-email.png);
}

.input._address:before {
  background-image: url(../images/icon-address.png);
}

.footer {
  position: relative;
  width: 100%;
  border-bottom: 24px solid #ff0000;
}

.footer__container {
  max-width: 1277px;
  margin: 0 auto;
  transform: translateZ(0);
}

@media (min-width: 640px) {
  .footer__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 68px;
  }
}

.footer__section {
  border-bottom: 1px solid #ececec;
}

@media (min-width: 640px) {
  .footer__section {
    margin-bottom: 24px;
    border: 0 none;
  }
}

@media (min-width: 640px) {
  .footer__section:not(:last-child) {
    padding-right: 20px;
  }
}

.footer__section._down {
  display: flex;
  align-items: flex-end;
}

.footer__title, .footer__made {
  position: relative;
  color: #000;
  font-size: 15px;
  font-family: "Neo Sans Pro", "Roboto", "Source Sans Pro", "Open Sans", Arial, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

@media (max-width: 639px) {
  .footer__title, .footer__made {
    height: 54px;
    padding: 21px 20px;
  }
}

@media (min-width: 640px) {
  .footer__title, .footer__made {
    padding-bottom: 8px;
  }
}

@media (max-width: 639px) {
  .footer__made {
    padding: 50px 20px 40px;
  }
}

.footer__toggle {
  position: absolute;
  opacity: 0;
  display: none;
}

@media (max-width: 639px) {
  .footer__toggle:checked ~ .footer__title:after {
    transform: rotate(180deg);
  }
}

@media (max-width: 639px) {
  .footer__toggle:checked ~ .footer__menu {
    height: 200px;
  }
}

.footer__title {
  position: relative;
  display: block;
}

@media (min-width: 640px) {
  .footer__title {
    pointer-events: none;
  }
}

@media (max-width: 639px) {
  .footer__title:after {
    content: '';
    position: absolute;
    width: 17px;
    height: 17px;
    top: 19px;
    right: 20px;
    background-image: url("data:image/false;base64,iVBORw0KGgoAAAANSUhEUgAAABEAAAARBAMAAADJQ1rJAAAAIVBMVEUAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAABt0UjBAAAACnRSTlMA7g3qjuiCfhcTGZ2pjQAAAEFJREFUCNdjIB0IK4ApZQGGVctADLZVCxgUVxkxMDBZLQRKea0qYKha5QCU4JRaqrpKEqyYZdWqJVDtjQsbGMgBAEQ7DRfwwyniAAAAAElFTkSuQmCC");
    background-repeat: no-repeat;
    transition: 0.3s transform;
  }
}

@media (max-width: 639px) {
  .footer__menu {
    height: 0;
    transition: 0.3s all;
    overflow: hidden;
  }
}

@media (min-width: 640px) {
  .footer__menu {
    padding-bottom: 8px;
  }
}

.footer__item {
  font-size: 15px;
  font-family: "Meta Pro", "Roboto", "Source Sans Pro", "Open Sans", Arial, sans-serif;
  font-weight: 400;
}

.footer__link {
  display: block;
}

@media (max-width: 639px) {
  .footer__link {
    padding: 0 20px;
    line-height: 44px;
  }
}

@media (min-width: 640px) {
  .footer__link {
    padding: 8px 0;
  }
}

.header__main {
  display: flex;
  height: 70px;
  background-color: #fff;
}

@media (max-width: 1049px) {
  .header__main {
    justify-content: space-between;
  }
}

@media (min-width: 1050px) {
  .header__main {
    height: 100px;
  }
}

